<template>
  <div class="play" :class="{ paused: paused }">
    <audio
      :src="
        'https://music.163.com/song/media/outer/url?id=' +
        currentMusic.id +
        '.mp3'
      "
      autoplay
      ref="audio"
    ></audio>

    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__slideInUp"
      leave-active-class="animate__animated animate__slideOutDown"
    >
      <div
        class="play-bar"
        v-show="isShowPlayBar"
        @click="isShowPlayBar = false"
      >
        <template v-if="playType === 2">
          <img :src="currentMusic.al.picUrl" alt="" />
          <h5>
            <p>{{ currentMusic.name }} -- {{ currentMusic.ar[0].name }}</p>
          </h5>
        </template>

        <template v-else-if="playType === 1">
          <img :src="currentMusic.picUrl" alt="" />
          <h5>
            <p>
              {{ currentMusic.name }} -- {{ currentMusic.song.artists[0].name }}
            </p>
          </h5>
        </template>

        <template v-if="playType === 3">
          <img :src="currentMusic.artists[0].img1v1Url" alt="" />
          <h5>
            <p>{{ currentMusic.name }} -- {{ currentMusic.artists[0].name }}</p>
          </h5>
        </template>

        <div class="control" @click.stop="togglePlayState">
          <canvas ref="circle" width="50" height="50"></canvas>
          <span class="icon">
            <div></div>
          </span>
        </div>
      </div>
    </transition>

    <transition
      name="custom-classes-transition"
      enter-active-class="animate__animated animate__fadeIn"
      leave-active-class="animate__animated animate__fadeOut"
    >
      <div class="play-full" v-if="!isShowPlayBar">
        <div
          class="play-fullbg"
          v-if="playType === 1"
          :style="{
            backgroundImage:
              'url(' +
              currentMusic.picUrl +
              '?imageView&thumbnail=50y50&quality=15&tostatic=0)',
            backgroundSize: '100% 100%',
            backgroundRepeat: 'no-repeat',
          }"
        ></div>
        <div
          class="play-fullbg"
          v-else-if="playType === 3"
          :style="{
            backgroundImage:
              'url(' +
              currentMusic.artists[0].img1v1Url +
              '?imageView&thumbnail=50y50&quality=15&tostatic=0)',
            backgroundSize: '100% 100%',
            backgroundRepeat: 'no-repeat',
          }"
        ></div>
        <div
          class="play-fullbg"
          v-else-if="playType === 2"
          :style="{
            backgroundImage:
              'url(' +
              currentMusic.al.picUrl +
              '?imageView&thumbnail=50y50&quality=15&tostatic=0)',
            backgroundSize: '100% 100%',
            backgroundRepeat: 'no-repeat',
          }"
        ></div>
        <PlayFullHeader
          @show-play-bar="isShowPlayBar = true"
          :currentMusic="currentMusic"
          :playType="playType"
        />
        <template>
          <PlayFullLyric
            v-if="isShowLyric"
            @toggle-show-lyric="isShowLyric = !isShowLyric"
            :currentTime="currentTime"
            :audio="this.$refs.audio"
            :currentWords="currentWords"

          />
          <PlayFullChart
            v-else
            @toggle-show-lyric="isShowLyric = !isShowLyric"
            :paused="paused"
            :currentMusic="currentMusic"
            :playType="playType"
          />
        </template>
        <PlayFullFooter
          v-bind:currentTime="currentTime"
          v-bind:duration="duration"
          @update:currentTime="$refs.audio.currentTime = $event"
        />
      </div>
    </transition>
  </div>
</template>

<script>
import PlayFullHeader from "@/components/PlayFullHeader.vue";
import PlayFullChart from "@/components/PlayFullChart.vue";
import PlayFullLyric from "@/components/PlayFullLyric.vue";
import PlayFullFooter from "@/components/PlayFullFooter.vue";
export default {
  props: ["currentMusic", "currentIndex", "playlist", "playType"],
  components: {
    PlayFullHeader,
    PlayFullChart,
    PlayFullLyric,
    PlayFullFooter,
  },
  data: function () {
    return {
      paused: null,
      duration: 0,
      currentTime: 0,
      isShowLyric: false,
      isShowPlayBar: true,
      currentWords: null,
    };
  },
  created() {
    console.log(this.currentMusic);
    this.axios
      .get("/lyric?id=" + this.currentMusic.id + "")
      .then((response) => {
        console.log("创建");
        this.currentWords = response.data.lrc.lyric;
        console.log(this.lyric);
      });
  },
  mounted() {
    console.log("mounted", this.$refs.audio);
    // console.log(this.$refs.usernameInput);
    let audio = this.$refs.audio;
    audio.addEventListener("pause", () => {
      console.log("暂停");
      this.paused = true;
    });
    audio.addEventListener("play", () => {
      console.log("播放");
      this.paused = false;
    });

    // var _this = this
    audio.addEventListener("durationchange", () => {
      // console.log("当前歌曲时长", this.duration);
      this.duration = audio.duration;
    });

    audio.addEventListener("timeupdate", () => {
      // console.log("当前播放到", this.currentTime);
      this.currentTime = audio.currentTime;

      this.drawCircle(this.currentTime, this.duration);
    });

    audio.addEventListener("ended", () => {
      // 播放完成下一曲
      this.playNext();
    });
  },
  // updated() {
  //   console.log("update", this.$refs.usernameInput);
  // },

  methods: {
    drawCircle: function (n, total) {
      let canvas = this.$refs.circle;
      // console.log(canvas);
      let ctx = canvas.getContext("2d");

      ctx.clearRect(0, 0, 50, 50);
      ctx.beginPath();
      ctx.strokeStyle = "rgba(0, 0, 0, 0.3)";
      ctx.arc(25, 25, 20, 0, Math.PI * 2, false); // 绘制
      ctx.stroke();
      ctx.closePath();

      ctx.beginPath();
      ctx.strokeStyle = "rgba(255, 0, 0, 0.6)";
      ctx.arc(
        25,
        25,
        19,
        Math.PI * -0.5,
        Math.PI * ((n / total) * 2 - 0.5),
        false
      ); // 绘制
      ctx.stroke();
      ctx.closePath();
    },

    togglePlayState: function () {
      let audio = this.$refs.audio;
      if (this.paused) {
        audio.play();
      } else {
        audio.pause();
      }
    },

    calculateNext: function () {
      // 根据当前播放模式 随机 单曲循环 顺序 顺序循环
      let nextIndex;
      if (this.currentIndex < this.playlist.length - 1) {
        nextIndex = this.currentIndex + 1;
      } else {
        nextIndex = 0;
      }

      return nextIndex;
    },

    playNext: function () {
      console.log("下一曲");
      // 获取当前播放索引
      // let currentIndex = this.playlist.findIndex(
      //   (song) => this.currentMusic.id === song.id
      // );
      // console.log(currentIndex);

      let index = this.calculateNext();

      this.$emit("update:music", {
        item: this.playlist[index],
        index,
      });
    },
    playPrev: function () {
      console.log("上一曲");
      // 获取当前播放索引
      // let currentIndex = this.playlist.findIndex(
      //   (song) => this.currentMusic.id === song.id
      // );
      // console.log(currentIndex);

      let index = this.calculatePrev();

      this.$emit("update:music", {
        item: this.playlist[index],
        index,
      });
    },

    calculatePrev: function () {
      // 根据当前播放模式 随机 单曲循环 顺序 顺序循环
      let prevIndex;
      prevIndex = this.currentIndex - 1;
      console.log(prevIndex);
      if (this.currentIndex <= 0) {
        prevIndex = this.playlist.length - 1;
        console.log(prevIndex);
      }

      return prevIndex;
    },
  },

  watch: {
    paused: function (n) {
      this.$emit("update:paused", n);
    },
    currentMusic: function () {
      // console.log(this.currentMusic);
      this.axios
        .get("/lyric?id=" + this.currentMusic.id + "")
        .then((response) => {
          console.log("换歌词");
          this.currentWords = response.data.lrc.lyric;
          console.log(this.lyric);
        });
    },
  },
  // computed: {
  //   solve: function () {
  //     var patt = /\[\d{2}:\d{2}\.\d{2,3}\]/gi;
  //     var arr = this.lyric
  //       .split("\n")
  //       .filter((e) => e)
  //       .map((str) => {
  //         var time = str.match(patt)[0].replace(/(\[|\])/gi, "");
  //         var timeArr = time.split(":");
  //         time = Number(timeArr[0]) * 60 + Number(timeArr[1]);
  //         var text = str.replace(patt, "");
  //         return { time, text };
  //       });
  //     return arr;
  //   },
  // },
};
</script>

<style scoped lang="less">
.play {
  &.paused {
    .play-bar {
      img {
        animation-play-state: paused;
      }
      .control {
        span.icon {
          div {
            background: url(../../img/暂停.png);
            width: 35px;
            height: 35px;
            background-size: cover;
            margin-left: 15%;
            margin-top: 15%;
          }
        }
      }
    }
  }
}
.play-bar {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 9;
  width: 100%;
  background: white;
  display: flex;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.15);
  img {
    width: 36px;
    height: 36px;
    margin: 7px;
    border-radius: 50%;
    animation: playing 6s linear infinite;
    box-shadow: 0 0 8px 0 rgba(0, 0, 0, 0.15);
  }
  h5 {
    flex: 1;

    p {
      margin-top: 20px;
      font-size: 10px;
    }
  }
  .control {
    position: relative;
    span.icon {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      // display: block;
      // background: rgba(240, 128, 128, 0.507);
      div {
        background: url(../../img/播放.png);
        width: 33px;
        height: 33px;
        background-size: cover;
        margin-left: 17%;
        margin-top: 17%;
      }
    }
  }
}
.play-full {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  .play-fullbg {
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
    filter: blur(20px);
    transform: scale(1.5);
  }
}
</style>