<template>
  <div class="enter">
    <div class="inputCover">
      <i></i>
      <input
        type="text"
        placeholder="搜索歌曲、歌手、专辑"
        @input="submit"
        v-model.trim="b"
      />
      <span class="deleteAll" @click="dAll">X</span>
    </div>

     <img src="../../img/loading.gif" v-if="isloading3"  style="display:block;margin:0 auto "/>
    <ul class="searchList" v-show="isShowList" v-else>
      <li v-for="(song, index) in results" :key="index" @click="showDetail(song.name)">
        <i></i><span>{{ song.name }}</span>
      </li>
    </ul>

    <ul class="detailList" v-show="isShowDetail">
      <h3>最佳匹配</h3>
      <li
        v-for="(item, index) in deatil"
        :key="index"
        :item="item"
        :index="index"
        @update:music="$emit('update:music', $event)"
        @update:playlist="$emit('update:playlist', hotSongs)"
        @play-type="$emit('play-type', $event)"
        @click="
          $emit('update:music', { item, index });
          $emit('update:playlist', deatil);
          $emit('play-type', 3);
        "
      >
        <div class="left">
          <div class="top">
            <span class="songName">{{ item.name }}</span>
          </div>
          <div class="bottom">
            <i class="icon"></i>
            <span>{{ item.artists[0].name }}</span
            >--<span> {{ item.album.name }}</span>
          </div>
        </div>
        <div class="right"><span class="playIcon"></span></div>
      </li>
    </ul>
    <div class="hotsearch" v-show="isShowHot">
      <h3>热门搜索</h3>
      <ul>
        <li
          v-for="(item, index) in hotWords"
          :key="index"
          @click='fastSearch(item)'
          
           
        >
          <a href="javascript:void(0);">{{ item }}</a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: ["hotWords","isloading3"],
  methods: {
    submit: function () {
      if (this.b =="" || this.b == undefined || this.b==null
      ) { 
        this.isShowList = false;
        this.isShowHot = true;
        this.isShowDetail=false;
        clearTimeout(this.timeOut);
        return;
      }
      else{ 
        
      if (!this.timeOut) {
        clearTimeout(this.timeOut);
      }
      this.timeOut = setTimeout(() => {

        this.axios.get("/search?keywords=" + this.b).then((response) => {
          console.log(response);
          // if (response.data.result == undefined) return;
          this.deatil = response.data.result.songs;
          this.results = response.data.result.songs;
          this.results = this.results.slice(0, 6);
        });

        clearTimeout(this.timeOut);
      }, 500);
      this.isShowList = true;
      this.isShowDetail = false;
      this.isShowHot = false;
      }
     
    },
    showDetail: function (songName) {
      this.isShowDetail = true;
      this.isShowList = false;
      this.isShowHot = false;
      this.b=songName;
    },
    fastSearch:function(word){
      this.b=word
      this.submit();
      this.isShowDetail=true;
      this.isShowList=false;
      this.isShowHot=false;
    },
    dAll:function(){
      this.b="";
      this.isShowDetail=false;
      this.isShowHot=true;
      this.isShowList=false;
    }
  },
  data() {
    return {
      results: [],
      timeOut: null,
      b: "",
      isShowDetail: false,
      isShowList: false,
      deatil: [],
      isShowHot: true,
      currentMusic: null,
      searchSongs: [],
    };
  },
  // watch: {
  //   bb(n) {
  //     this.b = n;
  //   },
  // },
};
</script>

<style scoped lang="less">
.enter {
  width: 100%;
  height: 60px;
  line-height: 18px;
  text-align: center;
  padding: 15px 10px;
  position: relative;
  .detailList {
    width: 100%;
    // background: blue;
    h3 {
      text-align: left;
      font-size: 12px;
      color: #666;
      line-height: 16px;
      margin-left: 10px;
    }
    li {
      margin-top: 20px;
      border-bottom: 1px solid rgba(0, 0, 0, 0.1);
      &::after {
        content: "";
        display: block;
        clear: both;
      }
      &:last-child{
        margin-bottom: 50px;
      }
      .left {
        // background: pink;
        float: left;
        width: 75%;
        .top {
          text-align: left;
          .songName {
            font-size: 17px;
          }
        }
        .bottom {
          text-align: left;
          .icon {
            display: inline-block;
            width: 12px;
            height: 8px;
            margin-right: 4px;
            background: url(//s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=)
              no-repeat;
            background-size: 166px 97px;
          }
          span {
            color: #888;
            font-size: 12px;
          }
        }
      }
      .right {
        float: right;
        width: 20%;
        .playIcon {
          width: 22px;
          display: inline-block;
          height: 22px;

          background: url(//s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=)
            no-repeat;
          background-size: 166px 97px;
          background-position: -24px 0;
        }
      }
    }
  }

  .searchList {
    width: 100%;
    background: white;

    li {
      display: flex;
      align-items: center;
      height: 45px;
      padding-left: 10px;

      i {
        margin-right: 7px;
        width: 15px;
        height: 15px;
        background-image: url(data:image/svg+xml;base64,);
      }
      span {
        color: #333;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
  }

  .inputCover {
    position: relative;
    width: 100%;
    height: 30px;
    padding: 0 30px;
    box-sizing: border-box;
    background: #ebecec;
    border-radius: 30px;
    i {
      position: absolute;
      left: 0;
      top: 9px;
      margin: 0 8px;
      vertical-align: middle;
      width: 13px;
      height: 13px;
      display: inline-block;
      background: url(data:image/svg+xml;base64,);

      background-position: 0 0;
      background-size: contain;
      background-repeat: no-repeat;
    }

    input {
      width: 100%;
      height: 30px;
      line-height: 18px;
      background: rgba(0, 0, 0, 0);
      font-size: 14px;
      color: #333;
      border-radius: 0;
      border: 0;
      outline: 0;
    }
  }
}
ul {
  background: rgb(252, 252, 253);
  margin-top: 15px;
}
.hotsearch {
  padding: 15px 0px 0;
  h3 {
    line-height: 12px;
    color: #666;
    font-size: 12px;
  }
  ul {
    margin: 10px 0px 7px;
    text-align: left;
    li {
      display: inline-block;
      height: 32px;
      margin-right: 8px;
      margin-bottom: 8px;
      padding: 0 14px;
      font-size: 14px;
      line-height: 32px;
      border-radius: 30px;
      border: 1px solid rgba(51, 51, 51, 0.1);
      a {
        color: #333;
        text-decoration: none;
      }
    }
  }
}
.deleteAll{
  background: #AAA;
  position: absolute;
  right: 3%;
  top: 7px;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  font-size: 8px;
  color: #ddd;
}
</style>