<template>
  <div class="playlist">
    <PlayListHead
      :currentPlayList="currentPlayList"
      :userName="userName"
      :userUrl="userUrl"
    ></PlayListHead>
      
       <h3>歌曲列表</h3>
    <PlayListList :playListSong="playListSong"
     @update:music="$emit('update:music', $event)"
      @update:playlist="$emit('update:playlist', playListSong)"
      @play-type="$emit('play-type',$event)"
       v-for="(item, index) in playListSong"
      :key="item.id"
      :item="item"
      :index="index"

    ></PlayListList>
  </div>
</template>

<script>
import PlayListHead from "@/components/PlayListHead.vue";
import PlayListList from "@/components/PlayListList.vue";

export default {
  components: { PlayListList, PlayListHead },
  created() {
    this.axios
      .get("/playlist/detail?id=" + this.$route.query.id + "")
      .then((response) => {
        console.log(response.data.playlist);
        this.currentPlayList = response.data.playlist;

        for (var i = 0; i < response.data.playlist.trackIds.length; i++) {
          this.songId.push(response.data.playlist.trackIds[i].id);
          this.axios
            .get(
              "/song/detail?ids=" + response.data.playlist.trackIds[i].id + ""
            )
            .then((response) => {
              // console.log(response.data.profile.avatarUrl);
              // console.log(response);
              this.playListSong.push(response.data.songs[0]);
            });
        }

        this.axios
          .get("/user/detail?uid=" + this.currentPlayList.userId + "")
          .then((response) => {
            // console.log(response.data.profile.avatarUrl);
            this.userName = response.data.profile.nickname;
            this.userUrl = response.data.profile.avatarUrl;
          });
      });
    console.log(this.$route.query.id);
  },
  watch: {
    "this.$route.query.id": function (n) {
      console.log(n);
    },
  },
  data() {
    return {
      currentPlayList: {},
      userId: "",
      userName: "",
      userUrl: "",
      playListSong: [],
      songId: [],
    };
  },
};
</script>

<style scoped lang="less">
.playlist {
  width: 100%;
  height: 246px;
  padding-top: 1px;
}
 h3 {
    height: 23px;
    line-height: 23px;
    padding: 0 10px;
    font-size: 12px;
    color: #666;
    background: #eeeff0;
  }
</style>