<template>
  <div class="search">
    <Enter
      :hotWords="hotWords"
      @update:music="$emit('update:music', $event)"
      @update:playlist="$emit('update:playlist', $event)"
      @play-type="$emit('play-type', $event)"
      :isloading3="isloading3"
    ></Enter>

    <!-- <HotSearch :hotWords="hotWords"
    @key-word="b=$event" v-show="false"></HotSearch> -->
  </div>
</template>

<script>
// import HotSearch from "@/components/HotSearch.vue";
import Enter from "@/components/Enter.vue";
export default {
  components: { Enter },
  data() {
    return {
      hotWords: [],
      allSongs: [],
      isloading3:true,
    };
  },
  created() {
    this.axios.get("/search/hot").then((response) => {
      // console.log(response.data.result.hots[0].first);
      for (var i = 0; i < response.data.result.hots.length; i++) {
        this.hotWords.push(response.data.result.hots[i].first);
      }
      // console.log(this.hotWords)
    }).finally(()=>{
          this.isloading3=false;
        });
  },
};
</script>

