<template>
  <div class="recommend">
    <RecommendPlayList :currentRecommends="currentRecommends"
    :isloading="isloading"
      >推荐歌单</RecommendPlayList
    >
    <RecommendNewMusic
      @update:music="$emit('update:music', $event)"
      @update:playlist="$emit('update:playlist', $event)"
      :newSongs="newSongs"
      :currentMusic="$attrs.currentMusic"
      :paused="$attrs.paused"
      @play-type="$emit('play-type', $event)"
      :isloading="isloading"
      >最新音乐</RecommendNewMusic
    >
  </div>
</template>

<script>
import RecommendPlayList from "@/components/RecommendPlayList.vue";
import RecommendNewMusic from "@/components/RecommendNewMusic.vue";

export default {
  components: {
    RecommendPlayList,
    RecommendNewMusic,
  },
  data: function () {
    return {
      recommends: [],
      newSongs: [],
      isloading: true,
    };
  },
  computed: {
    currentRecommends: function () {
      return this.recommends.slice(0, 6);
    },
  },
  created() {
    this.isloading = true;
    let num = 0;
    // console.log("created");
    this.axios
      .get("/personalized")
      .then((response) => {
        // console.log(response.data.result);
        this.recommends = response.data.result;
      })
      .finally(() => {
        num++;
        if (num == 2) {
      // console.log("总",num);
      this.isloading = false;
    }
      });

    this.axios
      .get("/personalized/newsong")
      .then((response) => {
        // console.log(response.data.result);
        this.newSongs = response.data.result;
        // console.log(this.newSongs)
      })
      .finally(() => {
        num++;
        if (num == 2) {
      // console.log("总",num);
      this.isloading = false;
    }
      });
    
    // console.log("created");
  },
};
</script>

