<template>
  <li
    class="song-item"
    @click="
      $emit('update:music', { item, index });
      $emit('update:playlist');
      $emit('play-type',1)
    "
  >
    <div class="info">
      <h5>
        <p class="songName">{{ item.name }}</p>
        
      </h5>
      <p>
       <span class="icon2"></span> <span v-for="(artist, index) in item.song.artists" :key="index">
          <template v-if="index"> / </template>{{ artist.name }}
        </span>
        --<span class="ablumName">{{item.song.album.name}}</span>
      </p>
    </div>
    <div class="icon">
      <span
        class="playing"
        :class="{ paused: paused }"
        v-if="currentMusic && currentMusic.id === item.id"
      >
        <i></i>
        <i></i>
        <i></i>
      </span>
      <span v-else><img src="../../img/bofang.png" alt=""></span>
    </div>
  </li>
</template>

<script>
export default {
  props: ["item", "index", "currentMusic", "paused"],
  // created() {
  //   console.log('$root',this.$root);
  //   console.log('$parent',this.$parent);
  // },
};
</script>

<style lang="less" scoped>
li.song-item {
  display: flex;
  background: rgb(252, 252, 253);
  padding: 10px 0;
  .info {
    flex: 1;
    span{
      font-size: 12px;
    }
  }
  .icon {
    width: 30px;
    height: 30px;
    
    .playing {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      i {
        width: 3px;
        height: 30px;
        background: red;
        // display: inline-block;
        transform-origin: center bottom;

        animation: playing 0.6s linear -0.2s infinite alternate-reverse;

        &:first-of-type {
          animation-delay: 0s;
        }
        &:last-of-type {
          animation-delay: -0.4s;
        }
      }
      &.paused {
        i {
          animation-play-state: paused;
        }
      }
    }
  }
}

@keyframes playing {
  from {
    // height: 50px;
    transform: scaleY(1);
  }
  to {
    // height: 10px;
    transform: scaleY(0.2);
  }
}
.icon2{
  background: url(//s3.music.126.net/mobile-new/img/index_icon_2x.png?5207a28c3767992ca4bb6d4887c74880=)
      no-repeat 0 0;
    display: inline-block;

    width: 12px;
    height: 8px;
    margin-right: 4px;
    background-size: 166px 97px;
}
li:last-child{
  margin-bottom:50px
}
.songName{
    overflow: hidden; // 超出的文本隐藏
text-overflow: ellipsis; // 溢出用省略号显示
white-space: nowrap; // 溢出不换行
width: 80vw;
}



</style>