<template>
  <div class="play-full-footer">
    <div class="ct">{{ min }}:{{ seconds }}</div>
    <div class="dt">{{ totalMin }}:{{ totalSecond }}</div>

    <input
      type="range"
      min="0"
      :max="duration"
      step="1"
      @input="getValue"
      :value="currentTime"
    />
    <div class="control">
      <div class="prev" @click.stop="playPrev"></div>
      <div
        class="paused1"
        @click.stop="togglePlayState"
        v-if="this.$parent.paused"
      ></div>
      <div class="paused2" @click.stop="togglePlayState" v-else></div>
      <div class="next" @click.stop="playNext"></div>
    </div>
  </div>
</template>

<script>
export default {
  props: ["currentTime", "duration", "pasued", "playType"],
  data: function () {
    return {
      min: 0,
      seconds: 0,
      totalMin: 0,
      totalSecond: 0,
    };
  },
  methods: {
    getValue: function (e) {
      console.log(e.target.value);
      this.$emit("update:currentTime", e.target.value);
    },
    togglePlayState: function () {
      this.$parent.togglePlayState();
    },
    playNext: function () {
      this.$parent.playNext();
    },
    playPrev: function () {
      this.$parent.playPrev();
    },
  },
  watch: {
    currentTime: function () {
      this.min = parseInt(this.currentTime / 60);
      this.seconds = parseInt(this.currentTime % 60);
    },
    duration: function () {
      this.totalMin = parseInt(this.duration / 60);
      this.totalSecond = parseInt(this.duration % 60);
    },
  },
  created() {
    this.totalMin = parseInt(this.duration / 60);
      this.totalSecond = parseInt(this.duration % 60);
  },
};
</script>

<style lang="less" scoped>
.play-full-footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  // input {
  //   width: 100%;
  //   display: block;
  // }
  .control {
    div {
      display: inline-block;
      // margin: 0 40px;
      padding: 10px 25px;
    }
    text-align: center;

    .next {
      background: url(../../img/下一首.png);
      background-size: 100%;
      height: 50px;
      width: 50px;
      margin-left: 20px;
    }
    .prev {
      background: url(../../img/上一首.png);
      background-size: 100%;
      height: 50px;
      width: 50px;
      margin-right: 20px;
    }
    .paused1 {
      background: url(../../img/播放222.png);
      background-size: 100%;
      height: 50px;
      width: 50px;
      //  margin-right: 20px;
    }
    .paused2 {
      background: url(../../img/暂停222.png);
      background-size: 100%;
      height: 50px;
      width: 50px;
    }
  }
}

input[type="range"] {
  -webkit-appearance: none;
  width: 70%;
  border-radius: 10px; /*这个属性设置使填充进度条时的图形为圆角*/
  margin-left: 15%;
}
input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
}

input[type="range"]::-webkit-slider-runnable-track {
  height: 3px;
  border-radius: 3px; /*将轨道设为圆角的*/
  // box-shadow: 0 1px 1px #def3f8, inset 0 .125em .125em #0d1112; /*轨道内置阴影效果*/
  background: #999;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
  height: 10px;
  width: 10px;
  margin-top: -4px; /*使滑块超出轨道部分的偏移量相等*/
  background: white;
  border-radius: 50%; /*外观设置为圆形*/
  border: solid 0.125em rgba(252, 254, 255, 0.5); /*设置边框*/
  box-shadow: 0 0.125em 0.125em #3b4547; /*添加底部阴影*/
}

.ct {
  position: absolute;
  left: 20px;
  width: 30px;
  height: 50px;
  top: 4px;
  font-size: 14px;
}
.dt {
  position: absolute;
  right: 14px;
  height: 20px;
  width:30px;
  top: 4px;
  font-size: 14px;
}
</style>